.nav {
  position: relative;
  display: flex;
  flex-wrap: wrap;
  max-width: 1366px;
}

.nav--closed .nav__list {
  @media ($mobile) {
    display: none;
  }
}

.nav--nojs .nav__toggler {
  @media ($mobile) {
    display: none;
  }
}

.nav--nojs .nav__list {
  @media ($mobile) {
    display: block;
  }
}

.nav__wrapper {
  width: 100%;
  min-width: 1024px;
  max-width: 1252px;
  margin: 0 auto;

  @media($tablet) {
    width: 88%;
    min-width: 678px;
    max-width: 1023px;
  }

  @media($mobile) {
    min-width: 290px;
    max-width: 768;
  }
}

.nav__logo {
  width: 15%;

  @media($tablet) {
    width: 19%;
  }

  @media($mobile) {
    width: 42%;
    margin-left: 23px;
  }
}

.nav a[href] {
  &:focus,
  &:hover {
    opacity: 0.6;
  }

  &:active {
    opacity: 0.3;
  }
}

.nav__toggler {
  display: none;

  @media($mobile) {
    position: absolute;
    top: 20px;
    left: 0;
    z-index: 2;
    display: block;
    width: 16px;
    height: 10px;
    background-color: transparent;
    border: none;
    cursor: pointer;
  }
}

.nav--closed .nav__toggler::before {
  content: "";
  position: absolute;
  top: -2px;
  left: 0;
  width: 16px;
  height: 2px;
  background-color: $color-lighter;
  box-shadow: 0 4px 0 0 $color-lighter, 0 8px 0 0 $color-lighter;
}

.nav--opened .nav__toggler {
  top: 18px;
  right: 0;

  &::before,
  &::after {
    content: "";
    position: absolute;
    top: 4px;
    left: -2px;
    width: 20px;
    height: 2px;
    background-color: $color-lighter;
  }

  &::before {
    transform: rotate(45deg);
  }

  &::after {
    transform: rotate(-45deg);
  }
}

.nav__list {
  display: flex;
  flex-wrap: wrap;
  justify-content: flex-start;
  flex-grow: 1;
  width: 58%;
  margin: 0;
  padding: 9px 0 0;
  list-style: none;

  @media($tablet) {
    justify-content: space-between;
    margin-right: 53px;
    padding: 4px 0 0 50px;
  }

  @media($mobile) {
    position: fixed;
    top: 0;
    left: 0;
    z-index: 1;
    display: block;
    // order: 4;
    width: 100%;
    margin: 0;
    padding: 35px 0 0 ;
    background-color: #F6F7FF;
    animation: bounce 0.6s;
  }
}

.nav__item {
  @media($tablet) {
    flex-grow: 1;
  }
}

.nav__link {
   margin-left: 25px;
   padding: 10px 0 10px 0;

  @media($tablet) {
    margin-left: 20px;
    padding:  0 0 10px 0;
  }

  @media($mobile) {
    display: block;
    width: 100%;
    margin: 0;
    padding: 20px 40px;
    text-align: center;
    color: $color-default;
    text-transform: uppercase;
  }
}

.nav__user-link {
  position: relative;
  width: 21%;
  padding: 10px 0 10px 50px;

  @media($tablet) {
    width: 5%;
    padding-left: 0;
  }

  @media($mobile) {
    margin-left: auto;
  }

  svg {
    position: absolute;
    top: 7px;
    left: 20px;

    @media($tablet) {
      top: 2px;
      left: 13px;
    }

    @media($mobile) {
      top: 7px;
      left: 0;
      width: 15px;
      height: 16px;
    }
  }

  span {
    @media($tablet) {
      display: none;
    }
  }
}
